<template>
  <div class="plr">
    <div ref="map" :id="id" class="w100 h100"></div>
    <div class="layer-box">
      <div @click="changeMap(0)" class="layer" :class="{active: activeIndex == 0}">
        <img src="./images/standardMap.svg" alt="">
        <span></span>
      </div>
      <div @click="changeMap(1)" class="layer" :class="{active: activeIndex == 1}">
        <img src="./images/satelliteMap.svg" alt="">
        <span></span>
      </div>
    </div>
    <slot></slot>
  </div>
</template>

<script>
import elementResizeDetector from "element-resize-detector";
import "ol/ol.css"; // 
import Map from "ol/Map"; // 
import View from "ol/View"; // 
import { Tile } from "ol/layer"; // 
import { XYZ } from "ol/source"; // 

export default {
  name: "viMap",
  data() {
    const webConfig = JSON.parse(sessionStorage.getItem("webConfig") || "{}");
    return {
      mapConfig: webConfig.map || {},
      id: `map_${Math.random().toString().slice(-3)}`,
      map: null,
      erd: null,
      activeIndex: 0,
    };
  },
  methods: {
    changeMap(activeIndex) {
      if (!this.map) return;
      const layers = this.map.getLayers().getArray();
      layers[activeIndex].setVisible(true);
      layers[1 - activeIndex].setVisible(false);
      this.activeIndex = activeIndex;
    },
    initMap() {
      const tileLayer = [
        new Tile({
          visible: true,
          source: new XYZ({
            maxZoom: 18,
            url: "http://wprd0{1-4}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&style=7&x={x}&y={y}&z={z}"
          }),
        }),
        new Tile({
          visible: false,
          source: new XYZ({
            maxZoom: 18,
            url: "http://wprd0{1-4}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&style=6&x={x}&y={y}&z={z}"
          }),
        })
      ];
      this.map = new Map({
        target: this.id,
        layers: tileLayer,
        view: new View({
          center: this.mapConfig.center,
          projection: "EPSG:4326", // 4326
          zoom: this.mapConfig.defaultZoom,
          minZoom: 8,
          maxZoom: this.mapConfig.maxZoom,
        }),
      });
      this.$emit("onMount", this.map);
    },
    resize() {
      if (!this.map) return;
      this.map.updateSize();
    },
  },
  mounted() {
    this.initMap();
    this.erd = elementResizeDetector();
    this.erd.listenTo(this.$refs.map, this.resize);
  },
  beforeUnmount() {
    this.erd.uninstall(this.$refs.map);
  },
};
</script>

<style lang="less" scoped>
.layer-box {
  position: absolute;
  right: 12px;
  top: 50px;
  border-radius: 4px;
  background: #FFFFFF;
  border: 1px solid #DDD;
  .layer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 4px;
    cursor: pointer;
    border-top: 1px solid #000;
    &:first-child {
      border-top: none;
    }
    img {
      width: 25px;
      height: 25px;
    }
    span {
      font-size: 12px;
    }
    &.active {
      span {
        color: #2d8cf0;
      }
    }
  }
}
</style>
